.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 8px;
  flex-wrap: wrap;

  &__item,
  & li {
    margin-bottom: 10px;

    &:not(:last-child) {
      margin-right: 8px;
    }

    &.active {
      span,
      a {
        background-color: $red;
        color: $white;
        font-weight: 700;
      }

      svg {
        fill: $white;
      }
    }
  }

  &__link,
  & a,
  & span {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: $white;
    transition: background-color .2s, color .2s;
    font-size: 14px;
    color: $black-light;
    transition: color .2s;
    font-weight: 600;

    svg {
      fill: $black;
      transition: fill .2s;
    }

    &:hover {
      background-color: $red;
      color: $white;
      font-weight: 700;

      svg {
        fill: $white;
      }
    }
  }
}